import React from 'react'
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import { makeStyles} from '@mui/styles';
import logo from "./NOGOLOGO.jpg"
import { IconButton } from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { styled, alpha } from '@mui/material/styles'
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
// /////////////////////////////////////////////////////////////////////////////////
import { Drawer} from '@mui/material'

import { List } from '@mui/material'
import { ListItem } from '@mui/material'
import { ListItemIcon } from '@mui/material'
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import ListItemText from '@mui/material/ListItemText';

import { useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import { Box } from '@mui/system';
import nogo from "./nogoConta.jpg"
import { ListItemButton } from '@mui/material';
import { Divider } from '@mui/material';
import ContactsIcon from '@mui/icons-material/Contacts';
import FmdGoodIcon from '@mui/icons-material/FmdGood';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import CollectionsBookmarkIcon from '@mui/icons-material/CollectionsBookmark';
import FaceIcon from '@mui/icons-material/Face';
import ShareIcon from '@mui/icons-material/Share';
import LiveHelpIcon from '@mui/icons-material/LiveHelp';
import BugReportIcon from '@mui/icons-material/BugReport';
import StarsIcon from '@mui/icons-material/Stars';


const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.black, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.black, 0.25),
    },
    marginLeft: "30%",
    marginRight:"2%",
    color:"black",
    width: 'auto',
    transition: 'ease .5s',
    ['@media (max-width:650px)']: {
      marginLeft: "2%",
      
      width: 'auto',
      rotate: '90deg',
        transition: 'ease .5s',
        display: 'flex'
    },
  }));
  
  const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }));
  
  const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
      padding: theme.spacing(1, 0, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(5)})`,
      transition: theme.transitions.create('width'),
      width: '100%',
      '&:focus': {
        width: '30ch'},
      ['@media (max-width:650px)']: {
        width: '12ch',
        '&:focus': {
          width: '12ch',
        },
      },
    },
  }));
  
  const useStyles=makeStyles((theme)=> {
    return{
      imgContainer:{
        width: "10rem",
        height:'5rem',
        flex:"1",
        textAnchor:"start",
        ['@media (max-width:650px)']: { // eslint-disable-line no-useless-computed-key
          width: "auto",
          height:"auto",
        
        }
        
        
      },
      logo:{
        objectFit:"contain",
        height: '100%', 
       borderRadius:'20px',
       ['@media (max-width:650px)']: { // eslint-disable-line no-useless-computed-key
        width: '4.5rem',
        height:"3rem",
        marginTop:"5px"
      }
       
      },
      toolbar:{
        backgroundColor:"white",
        padding:"15px"
      },
      cart:{
        margin:"0 3rem"
      },
      drawer:{
          width:"240px",
      },
      nogo:{
        objectFit:"contain",
        width:"100%",
      },
      conticon:{
        color:"black"
      }

    }
    
  })
  

const Header = () => {
  const [open,setOpen]=useState(false);
    const classes=useStyles();
    return (
        <div>
           <AppBar
    position='static'
    >
    <Toolbar
    className={classes.toolbar}
    >
      <div className={classes.imgContainer}>
      <a href="#"><img src={logo} className={classes.logo}></img></a>
      </div>
      <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search…"
              inputProps={{ 'aria-label': 'search' }}
            />
      </Search>
      
      <div>
        <IconButton className={classes.cart}>
          <ShoppingCartIcon
          fontSize="large"
          color="info"
           />
        </IconButton>
      </div>

      <IconButton
            onClick={()=>{setOpen(true)}}
            className={classes.burger}
            >
              <MenuIcon fontSize="large"></MenuIcon>
            </IconButton>

    </Toolbar>
    </AppBar>
    
            <Drawer
            className={classes.drawer}
            variant="temporary"
            anchor="right"
            open={open}
            onClose={()=>setOpen(false)}
            >
             <Box
             width="240px"
             >

              <img className={classes.nogo} src={nogo} />
             </Box> 
        <Divider className={classes.conticon} />           
        <List>
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <ContactsIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Profile" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <FmdGoodIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Address" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <LocalShippingIcon  className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Order" />
            </ListItemButton>
          </ListItem>
          <Divider className={classes.conticon} />

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <CollectionsBookmarkIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Coupons" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <FaceIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Favourites" />
            </ListItemButton>
          </ListItem>

          <Divider className={classes.conticon} />
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <ShareIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Share" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <LiveHelpIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Help" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <BugReportIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Report Issue" />
            </ListItemButton>
          </ListItem>

          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
               <StarsIcon className={classes.conticon} />
              </ListItemIcon>
              <ListItemText primary="Rate Site" />
            </ListItemButton>
          </ListItem>

          </List>

            </Drawer> 
        </div>
    )
}

export default Header
