import React from 'react'
import latestNovel from "./LatestNovel"
import BookDisplay from '../Book-Display/BookDisplay'
import { Container } from '@mui/material'

const LatestNovel = () => {
    return (
        <Container>
        <h2>Latest Novels</h2>
            <div className="booksAlign">
            <BookDisplay book={latestNovel} />
        </div>
        </Container>
    )
}

export default LatestNovel
