import { Container } from '@mui/material'
import React from 'react'
import "./BookDisplay.css";

const BookDisplay = ({book}) => {
    return (
       <div className="bookDisplay__flex">
           
           {book.map((item,index)=>(
                <a href="" className="bookDisplay">
            <div className="bookDisplay__imgContainer">
                <img className="bookDisplay__img" src={item.url}>

                </img>
            </div>

            <p className="bookDisplay__subtitle">{item.name}</p>
            <div><span className="bookDisplay__price">Rs{item.price}</span><span className="bookDisplay__originalPrice">Rs{item.originalPrice}</span></div>
            <br />
            <span className="bookDisplay__discount">{item.discount}.0% off</span>
        </a>
            ))}
       </div>
   
    )
}

export default BookDisplay
