import React from 'react'
import { Container,Divider } from '@mui/material'
import "./NovelForYou.css";
import nyf from "./nfy.jpg"
const nyfImages=[
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   {
      name:'romance',
      path:nyf
   },
   
]
const NovelForYou = () => {
    return (
       <Container>
         
        <div className="Novelforyou">

        <h2>Novel for you</h2>

           <div className="nfy_display">

           {nyfImages.map((image,index)=>(
               <a className="nfy_anchor" href="">
               <div key={index} className="nfy__imgContainer">

                     <img  className="nfy__img" src={image.path} />

               </div>
               </a>
            ))}  

           </div>    
      
        </div>
            <Divider />
       </Container>
    )
}

export default NovelForYou
