import React from 'react';
import "./SubButton.css"
import { Container, Divider } from '@mui/material';

const classsort=[
    '9','10','11','12','1-8'
]
const examsort=[
    'IIT','NEET','ICSE','Govt.Exam','ENNG.'
]
const SubjectButtons = () => {
    return (
        <Container>
            <div className="subbutton">
            <div className="subbutton__classSort">
               
                    {classsort.map((item,index)=>(
                        <a href="#">

                        <div className="subbutton__btnWrapper">
                        <div className="subbutton__btn">
                            <p key={index}>{item}</p>
                        </div>
                        <p>Class {item}</p>
                        </div>

                        </a>
                        
                    ))}
                
            </div>
            <div className="subbutton__examSort">
                    {examsort.map((item,index)=>(
                        <a href="#">

                        <div className="subbutton__btnWrapper">
                        <div className="subbutton__btn">
                            <p className="subbtn__resize" key={index}>{item}</p>
                        </div>
                        <p>{item}</p>
                        </div>

                        </a>
                        
                    ))}
            </div>
        </div>
        <Divider />
        </Container>
    )
}

export default SubjectButtons
